function createData1(data1, data2, data3, data4, data5, data6, data7, data8) {
  let rowclass;
  if(data8 == '') {
    rowclass='condition1';
  } else if(data8 == '') {
    rowclass='condition3';
  } else if(data8 == '') {
    rowclass='condition5';
  } else if(data8 == '') {
    rowclass='condition6';
  } else {
    rowclass=''
  }
  return { data1, data2, data3, data4, data5, data6, data7, data8, rowclass };
}

